
import $ from 'jquery'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import Loading from 'vux/src/components/loading/index.vue'

import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import upload from '../Plug/upload.vue'
export default {
directives: {
TransferDom
},
data(){
return {
show2:false,
date: '',
title1: '类型',
list1: [],
value1: [],
formData:{
csrfmiddlewaretoken:"",
type_id: null,
goods_name:null,
goods_price:null,
dec:null,
guige:null
},
hadSelImg:false,
uping:false
}
},
components: {
Group,
Cell,
CellBox,
upload,
XTextarea,
PopupPicker,
XInput,
XButton,
Loading
},
mounted(){

this.getTypes()
},
activated () {
if (window.mycache.standards) {
this.formData.guige=window.mycache.standards
}
console.log(window.mycache.standards)

},
methods:{
changes(value){
console.log(value)
},
getTypes(){
this.$http.get('http://localhost:8000/getGoodsTypes').then(response => {
this.list1=[this.querySetToArray(response.data)]
console.log(this.list1)




}, response => {
console.log("error");
});
},
querySetToArray(obj){
var newObj=[]
for( var i=0;i<obj.length;i++){
newObj.push(
{
value:obj[i].pk,
name:obj[i].fields.name
})
}
return newObj
},
submit_goods:function() {
var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
if (!this.hadSelImg) {
alert("请上传一张商品图片")
return
}
if (this.formData.goods_name==null||this.formData.goods_name.replace("",' ').length<1) {
alert("请输入商品名称")
return
}
if (this.formData.goods_price==null||this.formData.goods_price.replace("",' ').length<1) {
alert("请输入商品价格")
return
}
if (!reg.test(this.formData.goods_price)) {
alert("价格格式不正确")
return
}
if (this.formData.dec==null&&this.formData.dec.replace("",' ').length<1) {
alert("请输入商品描述")
return
}
this.formData.type_id=this.value1[0]

var senData = this.obj_copy(this.formData)
senData.guige = JSON.stringify(senData.guige)

this.uping=true
this.$refs.upload.uploadImg(null,senData)
},
wancheng(){
this.uping=false
this.$router.push({
name:"goods"

}
)
},
obj_copy(old){
var newobj ={}
for(var key in old ){
newobj[key]=old[key]
}
return newobj
}


}
}



